body {
  margin: 0;
  overflow-x: hidden;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

a {
  text-decoration: none;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.wallet-bg::after,
.wallet-bg::before {
  border-radius: 50%;
  content: "";
  display: block;
  filter: blur(6rem);
  height: 60vh;
  position: fixed;
  width: 60vh;
  z-index: -1;
}
.wallet-bg::before {
  background: linear-gradient(276.79deg, #e6e2ff 0, #fff0e2 100%);
  right: 30vh;
  top: 0;
  transform: matrix(-0.9, -0.5, 0.1, -0.9, 0, 0);
}
.wallet-bg::after {
  background: linear-gradient(276.79deg, #e2ffe5 0, #e5f6ff 30%, #e6e2ff 100%);
  bottom: 30vh;
  left: 30vh;
  transform: matrix(-0.9, 0.5, -0.1, -0.9, 0, 0);
}
